.footer-wrap {
  margin-top: 30px;

  .foot-box {
    width: 1000px;
    margin: 0 auto;
    font-size: 12px;
    text-align: center;
  }
  .foot-name {
    // border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    width: 100%;
    padding: 10px 0;
  }
  .ni-box {
    width: 720px;
    margin: 0 auto;
  }
  .foot-ni {
    box-sizing: border-box;
    width: 120px;
    border-right: 1px solid #ddd;
  }

  .foot-sec {
    margin-top: 20px;
    text-align: left;
    &>a {
      color: #999;
      padding: 5px;
    }
  }

  .copyright {
    line-height: 44px;
    width: 280px;
    margin-top: 10px;
    // margin: 10px auto 0 auto;
    position: relative;
    cursor: pointer;
    user-select: none;
  }
  .logo-bdqn {
    white-space:nowrap;
    &>img {
      width: 150px;
    }
  }

  .copy {
    position: absolute;
    left: 110px;
    top: 13px;
    color: #e26570;
    font-weight: bold;
  }

}
