@charset "UTF-8";
/**
 * 登录页
 */
.login-head {
  height: 100px;
  width: 1200px;
  margin: 0 auto;
  padding: 10px 0; }

.main-logo {
  height: 100%; }
  .main-logo > img {
    height: 100px; }

.login-msg {
  background: #f5e3ff;
  border: 1px solid #c19ad0;
  color: #666;
  font-size: 12px;
  padding: 5px 40px;
  margin-top: 35px; }

.login-wrap {
  background-color: #ba34eb;
  background: url("http://118.126.108.36/image/seller/login_wall.jpg") no-repeat center; }

.login-title {
  height: 38px; }

.login-form {
  height: 475px;
  width: 990px;
  margin: 0 auto;
  position: relative; }
  .login-form .form {
    position: absolute;
    right: 0;
    top: 50%;
    margin-top: -168px;
    height: 320px;
    width: 350px;
    background-color: #f6e8ff; }

.form-t {
  padding: 20px 0;
  border-bottom: 1px solid #f4f4f4;
  font-size: 18px; }
  .form-t::after {
    content: "";
    display: block;
    clear: both; }
  .form-t div {
    width: 50%;
    text-align: center;
    cursor: pointer; }
    .form-t div:hover {
      color: #c779f1;
      font-weight: bold; }
  .form-t .f1 {
    color: #ba34eb;
    font-weight: bold; }
  .form-t .f2 {
    color: #666; }
  .form-t .t-l {
    float: left;
    box-sizing: border-box;
    border-right: 1px solid #f4f4f4; }
  .form-t .t-r {
    float: right; }

.to-registory {
  text-align: right;
  color: #333;
  font-size: 12px;
  padding: 0 20px;
  position: absolute;
  left: 10px;
  bottom: 30px; }

/**
 * 手机注册页
 */
.l-input-wrap {
  width: 304px;
  margin: 20px auto; }
  .l-input-wrap label {
    width: 39px;
    height: 40px;
    float: left;
    display: block;
    border-right: 1px solid #ddd; }
  .l-input-wrap input {
    display: block;
    float: left;
    height: 40px;
    border: 0;
    padding: 0 8px;
    box-sizing: border-box; }
  .l-input-wrap > div {
    width: 306px;
    height: 40px;
    margin-bottom: 20px;
    border: 1px solid #ddd; }
  .l-input-wrap .acc-log-err {
    border: 0;
    color: red;
    text-align: right;
    height: 20px;
    margin: 0;
    margin-top: -10px;
    font-size: 12px; }
  .l-input-wrap .acc-log-err-submit {
    margin-top: 70px; }

.log-input1 input {
  width: 265px;
  outline: none; }

.account-log-submit {
  display: block;
  margin: 0 0 0 20px;
  width: 250px;
  height: 38px;
  background-color: #ba34eb;
  font-size: 16px;
  color: #fff;
  text-align: center;
  line-height: 40px;
  border-radius: 5px;
  border: 0;
  cursor: pointer; }

.log-input2 {
  position: relative; }
  .log-input2 input {
    width: 265px;
    outline: none; }

.log-sendsms {
  width: 100px;
  height: 40px;
  position: absolute;
  right: 0;
  cursor: pointer;
  background: #d0b5f9;
  border: none;
  color: #666;
  text-align: center;
  line-height: 40px;
  outline: none; }
