.issue {
  background: #f7f8fa;

  .require {
    position: relative;
    padding: 0 10px;
    &::before {
      content: "*";
      color: #f40;
      margin-top: 10px;
      position: absolute;
      bottom: -4px;
      left: 0px;
      font-weight: bold;

    }
  }
  .f-important {
    color: #ff6600;
  }

  .curr-item {
    font-weight: bold;
    color: #ff5400;
  }
  .curr-gt {
    margin: 0 20px;
    color: #7d6767;
  }

  .issue-head {
    background: #fff;
    padding: 10px 0 20px 0;
    .f1 {
      color: #f40;
      font-size: 30px;
    }
    .f2 {
      color: #333;
     font-size: 20px;
    }
   }
   .ih-box {
     width: 1000px;
     margin: 0 auto;
   }

   .issue-body {
      width:1000px;
      margin: 0 auto;
   }

   .issue-item {
     margin-top: 30px;
     background-color: white;
     padding: 20px;
   }
   .iss-tit {
     padding-bottom: 10px;
     border-bottom: 1px solid #ccc;
   }

   .iss-mini-wrap {
    padding: 0 40px;
   }
   .iss-detail {
     padding: 20px 0;
     line-height: 25px;
   }

   .good-tit {

    input[type=text] {
      width: 760px;
      height: 25px;
      padding: 0 7px;
    }
   }
   .iss-spec {
     padding: 0 0 10px 0;
   }
   .prop-wrap {
    width: 700px;
    margin: 0 auto;
    input {
      height: 20px;
      width: 250px;
      padding: 3px 7px;
      margin: 0 0 7px 0;
      color: #555;
      border-color: #ccc;
      border: 1px dashed #aaa;
      &:hover+.prop-cancel {
        display: block;
      }
     }
   }
  .iss-onece {
    padding: 10px 0;
  }
  .prop-name-wrap {
    position: relative;
    &:hover .prop-cancel {
      display: block;
    }
  }

  .prop-value-wrap {
    margin-left: 20px;
  }
  .prop-value {
    position: relative;
  }
   .prop-cancel {
     display: none;
     width: 16px;
     height: 16px;
     cursor: pointer;
     position: absolute;
     right: 5px;
     top: 6px;
     &:hover {
       display: block;
     }
   }
   .iss-table-wrap {
     margin: 30px 0 10px 0;
   }
   // start 表格
   .issue-table {
      border-collapse: collapse;
      tr {

      }
      td {
        padding: 5px 10px;
      }
      input[type=text] {
        border: 0;
      }
   }
  .issue-thead {
    background: #e4f2f5;
    font-weight: bold;
  }

   // end 表格

   //start 图片
   .iss-pics-wrap {
     padding-bottom: 80px;
   }
   .iss-picture-wrap {

   }
   .iss-pic-item-wrap {
     margin-top: 40px;
   }
   .iss-pic-tit {
     padding: 10px 20px;
     color: #444;
     font-size: 14px;
     font-weight: bold;
     &::before {
       content: "*";
       display: inline;
       color: #f40;
       padding: 0 3px;
     }
   }
   // end 图片
   .pic-smaill-wrap {

   }
   .pic-item {
      position: relative;
      width: 140px;
      height: 140px;
      border: 2px solid #ccc;
      overflow: hidden;
      line-height: 150px;
      text-align: center;
      margin: 0 19px;
      padding: 5px;
      img {
        max-width: 140px;
        position: relative;
        z-index: 10;
      }
      input[type=file] {
        position: absolute;
        z-index: 99;
        bottom: 0;
        left: 0;
        width: 140px;
        height: 140px;
        opacity: 0;
      }
      &::before {
        content: "点击添加图片";
        display: block;
        color: #666;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 0;

      }
      .pic-cancel {
        display: block;
        position: absolute;
        z-index: 9999;
        width: 150px;
        height: 30px;
        bottom: 0;
        left: 0;
        color: #fff;
        text-align: center;
        line-height: 30px;
        background: rgba(0,0,0,.5);
        cursor: pointer;
        display: none;
      }
      &:hover .pic-cancel{
        display: block;
      }
    }

  .iss-submit {
    padding-bottom: 50px;
    background: #fff;
    button {
      display: block;
      margin: 0 auto;
      width: 200px;
      height: 50px;
      border-radius: 8px;
      border: 0;
      background: #f40;
      color: #fff;
      font-weight: bold;
      cursor: pointer;
    }
  }
}
