@charset "UTF-8";
.category .category-wrap {
  width: 1200px;
  margin: 0 auto; }

.category .category-head {
  margin: 25px 0; }

.category .cho-wrap {
  width: 820px;
  margin: 0 auto; }
  .category .cho-wrap::after {
    content: "";
    display: block;
    clear: both; }

.category .cho-onece {
  width: 240px;
  height: 400px;
  padding: 10px 0 10px 10px;
  overflow-y: auto;
  float: left;
  margin: 0 5px;
  border: 1px solid #3d33cc; }
  .category .cho-onece::-webkit-scrollbar {
    -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
    width: 10px;
    /*高宽分别对应横竖滚动条的尺寸*/
    height: 1px;
    border-radius: 10px;
    background: #eee; }

.category .cho-item {
  padding: 2px 0 2px 5px;
  margin-right: 10px;
  cursor: pointer;
  border: 1px solid transparent; }
  .category .cho-item:hover {
    background-color: rgba(84, 84, 170, 0.1); }

.category .cho-chosen {
  background-color: rgba(53, 53, 255, 0.1) !important;
  color: #444;
  font-weight: bold;
  border: 1px solid rgba(32, 32, 180, 0.2); }

.category .current-cate-wrap {
  width: 820px;
  margin: 20px auto; }

.category .curr-cate-box {
  width: 770px;
  padding: 10px;
  background: #e0ecff; }

.category .curr-item {
  font-weight: bold;
  color: #ff5400; }

.category .curr-gt {
  margin: 0 20px;
  color: #7d6767; }

.category .c-submit {
  height: 40px;
  width: 300px;
  border-radius: 4px;
  font-size: 16px;
  font-weight: bold;
  border: 1px solid #fff;
  padding: 5px;
  margin: 0 auto;
  background-color: #f40;
  text-align: center;
  line-height: 40px;
  cursor: pointer;
  user-select: none;
  background-color: #4096e7;
  color: white;
  margin-top: 30px; }

.category .c-submit-disable {
  background-color: #f4f4f4;
  color: #aaaaaa; }

.category .readme {
  border: 1px solid #ccc;
  padding: 5px 10px 5px 20px;
  overflow-y: auto;
  width: 820px;
  height: 200px;
  margin: 30px auto 50px auto; }
