.home {
  // width: 1200px;
  // margin: 0 auto;
  background: #eee;

  .h-f1 {
    font-size: 26px;
  }
  .h-f2 {
    font-size: 18px;
    padding: 0 10px;
  }

  .head-wrap {
    background: rgb(54,128,241);
    color: #fff;
  }
  .head-content {
    width: 1200px;
    margin: 0 auto;
    height: 50px;
    line-height: 45px;
  }
  .head-r {
    width: 300px;
    text-align: center;
    line-height: 50px;
  }
  .h-r-item {
    margin-left: 10px;
  }

  .h-body {
    width: 1200px;
    margin: 20px auto 0 auto;
  }

  .list1-wrap {
    width: 200px;
    height: 477px;
    background: #fff;

  }
  // start 会员信息
  .user-info-wrap {
    background: #f5f8fa;
    border-bottom: 1px solid #e4eaee;
    padding: 10px 10px 10px 10px;
    // border-bottom: 1px solid #ccc;
    position: relative;
    user-select: none;
  }
  .u-avatar {
    width: 60px;
    height: 60px;
    &>img {
      width: 60px;
      height: 60px;
      border-radius: 50%;
    }
  }
  .u-storename-wrap {
    width: 120px;
  }
  .u-storename {
    text-align: right;
    padding: 10px 8px;
    font-size: 14px;

    .f2 {
      color: #5834e2;
      padding: 5px 10px;
      font-weight: bold;
      position: relative;
      &::before {
        content:"";
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 30px;
        height: 10px;
        border-top: 2px solid #3dcaa0;
        border-left: 2px solid #3dcaa0;

      }
      &::after {
        content:"";
        display: block;
        position: absolute;
        right: 0;
        bottom: 0;
        width: 30px;
        height: 10px;
        border-right: 2px solid #ffb295;
        border-bottom: 2px solid #ffb295;
      }
    }
  }
  .chat-bar {
    cursor: pointer;
    width: 40px;
    height: 40px;
    // text-align: right;
    // margin-top: 7px;
    // margin-left: 10px;
    position: absolute;
    left: 52px;
    bottom: 2px;
    &>img {
      width: 30px;
      height: 30px;
      margin-top: 5px;
    }
  }
  .notread-msg {
    position: absolute;
    width: 16px;
    height: 16px;
    text-align: center;
    line-height: 16px;
    left: 10px;
    bottom: 0px;
    background: #ff0036;
    color: white;
    border-radius: 50%;
    padding: 2px;
    font-size: 12px;
  }
  // end 会员信息

  // start 操作菜单-导航条
  .oper-wrap {
    padding: 10px;
  }
  .nav-item-wrap {
    padding: 10px 0;

  }
  .nav-item {
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
    user-select: none;
  }

  .oper-bar {
    &:hover {
      color: #000;
    }
  }
  .bar-seleced {
    color: #000;
  }

  .nav-logo {
    width: 21px;
    height: 22px;
    padding: 0 10px;
    &>img {
      width: 21px;
      height: 22px;
    }
  }
  .nav-icon {
    margin-right: 10px;
    &>img {
      width: 12px;
      height: 12px;
    }
  }

  .nav-box {
    margin-left: 36px;
  }
  .nav-cho-i {
    padding: 5px;
    cursor: pointer;
    user-select: none;
  }
  // end 操作菜单-导航条


  // start 实时数据
  .current-data {
    width: 434px;
    margin: 0 10px;
  }
  .data-wrap {
    background: #fff;
  }
  .da-head {
    font-size: 16px;
    font-weight: bold;
    line-height: 50px;
    padding: 0 30px;
  }
  .data-box {
    border: 1px solid #ddd;
    padding: 10px;
  }
  .data-item {
    box-sizing: border-box;
    width: 200px;
    height: 135px;
    padding: 10px 20px;
  }
  .border-right {
    border-right: 1px solid #ddd;
  }
  .border-bottom {
    border-bottom: 1px solid #ddd;
  }
  .da-logo,.da-logo>img {
    width: 40px;
    height: 40px;

  }
  .da-tit {
    line-height: 40px;
  }
  .da-amount {
    text-align: right;
    line-height: 50px;
    color: #2a9eff;
  }
  // end 实时数据


  // start 待办数据
  .backlog {
    width: 526px;
    background-color: #fff;
    // padding: 10px;
  }
  .log-body {
    padding: 10px 30px 0 30px;
  }
  .backlog-item {
    margin-bottom: 7px;
  }
  .log-item {
    line-height: 40px;
    margin-right: 30px;
    width: 110px;
  }
  .log-tit {
    font-size: 15px;
    font-weight: bold;
    color: #333;
  }
  .log-amount {
    color: #2a9eff;
  }
  .log-self {
    line-height: 40px;

  }
  // end 待办数据

  // start OrderManager
  .om-wrap {
    margin-left: 30px;
    background: #fff;
    padding: 10px 20px;
    min-height: 457px;
  }
  .om-head {
    // margin-bottom: 30px;
    padding: 10px 0;
    font-size: 14px;
    color: #000;
    font-weight: bold;
    border-bottom: 2px solid #dbd9e2;
  }
  // end OrderManager

  // start sendPost
  .post-wrap {
    position: fixed;
    top: 40%;
    left: 50%;
    margin-left: -150px;
    margin-top: -79px;
    background-color: rgba(0,0,0,.3);
    width: 300px;
    height: 158px;
    padding-bottom: 0px;
    input {
      height: 20px;
    }
    label {
      color: #fff;
      font-weight: bold;
    }
  }
  .post-field {
    margin: 20px;
    input[type=text] {
      padding: 0 6px;
    }
  }
  .post-operator {
    width: 200px;
    margin: 0 auto;
    button {
      display: block;
      margin: 0 auto;
      width: 80px;
      height: 30px;
      background: #ec6060;
      border: 0;
      color: #fff;
      cursor: pointer;
    }
  }
  button.post-cancel {
    background: #a6a6ce;
  }

  .item-empty {
    text-align: left;
    padding: 30px;
    font-size: 16px;
  }


  // end sendPost
}

