.regist {

  .regist-head {
    padding-top: 30px;
    width: 1000px;
    margin: 0 auto;
  }
  .regi-head-box {
    width: 240px;
    position: relative;
    user-select: none;
  }
  .f-logo {
    height: 60px;
    img {
      height: 60px;
    }
  }
  .f-tit {
    font-size: 20px;
    line-height: 40px;
    vertical-align: bottom;
    position: absolute;
    bottom: 0;
    right: 0;
  }

  .regist-body {
    width: 1000px;
    margin: 0 auto;
  }
  .step-wrap {
    border-bottom: 2px solid #ccc;
    padding: 15px 200px 0 200px;
  }
  .step-one {
    width: 200px;
    text-align: center;
    font-size: 16px;
    color: #aaa;
    font-weight: bold;
    height: 50px;
    line-height: 50px;
    padding-bottom: 10px;
    border-bottom: 2px solid #ccc;
    margin-bottom: -2px;
  }
  .f-icon {
    background: #aaa;
    color: #fff;
    font-weight: bold;
    padding: 2px 9px;
    border-radius: 50%;
    margin-right: 6px;
  }
  .f-flush {
    .f-icon {
      background: #ff4000;
    }
    .f-step {
      color: #000;
    }
    &.step-one {
      border-bottom: 2px solid #f40;
    }
  }

  .step1-wrap {
    width: 284px;
    margin: 90px auto;

    input {
      width: 200px;
      height: 40px;
      padding: 0 7px;
      font-size: 14px;
    }
    .sms-input {
      width: 120px;
    }
    .letter {
      letter-spacing: 3px;
    }
  }

  .form-err {
    color: red;
    text-align: right;
    line-height: 20px;
  }
  .step1-item {
    margin: 20px 0;
    width: 284px;
    position: relative;
    line-height: 30px;
    label {
      padding-right: 10px;
    }
  }

  .phone-item {
    // padding-bottom: 20px;
  }
  .sms-item {
    padding-top: 20px;

  }
  .sms-input{

  }
  .sms-btn {
    width: 82px;
    height: 44px;
    background: #57565f;
    // border: 1px solid #57565f;
    border: 0;
    color: #fff;
    position: absolute;
    right: 0;
    bottom: 0px;
    cursor: pointer;
    font-weight: bold;
  }
  .next-btn {
    display: block;
    margin: 0 auto;
    height: 44px;
    width: 150px;
    border: none;
    background: #FF0000;
    border-radius: 3px;
    // margin: 20px 0px 0px 75px;
    color: #fff;
    cursor: pointer;
    outline: none;
    margin-top: 30px;
  }
  .next-wait {
    background: #ccc;
  }

  // start step2
  .step2-wrap {
    width: 430px;
    margin: 50px auto 0 auto;
    line-height: 36px;
    input[type=text] {
      width: 320px;
      padding-left: 10px;
      height: 36px;
    }
  }
  .step2-item {
    width: 430px;
    padding: 20px 0;
  }

  .step3-wrap {
    width: 800px;
    margin: 0 auto;
    text-align: text;
    text-align: center;
  }
  .s3-body {

  }
  .s3-info {
    width: 500px;
    margin: 0 auto;
    font-size: 16px;
    border: 1px solid red;
    margin-top: 100px;
    padding: 20px 0;
    // box-shadow: 0 0 2px #f40 insert;
    -moz-box-shadow:0 0 5px #f40 inset;               /* For Firefox3.6+ */
    -webkit-box-shadow:0 0 5px #f40 inset;            /* For Chrome5+, Safari5+ */
    box-shadow:0 0 5px #f40 inset;

  }
  .s3-storeName {
    color: #f40;
    padding: 0 10px;
    font-weight: bold;
  }
  .s3-ok {
    height: 30px;
    width: 150px;
    border: none;
    background: #FF4400;
    border-radius: 3px;
    color: #fff;
    cursor: pointer;
    margin-top: 50px;
    margin-bottom: 50px;
  }
}
