  background: rgba(0, 0, 0, 0.5);
  color: #fff;
  position: fixed;
  left: 50%;
  top: 50%;
  margin-left: -140px;
  margin-top: -60px;
  z-index: 9999;
  border-radius: 18px 0 18px 0;
  box-shadow: 5px 5px 10px #ccc; }
  .confirm-box .msg-content {
    box-sizing: border-box;
    padding: 10px;
    height: 90px;
    text-align: center;
    line-height: 30px;
    font-size: 16px;
    font-weight: bold;
    vertical-align: middle;
    overflow: hidden; }
  .confirm-box .btn-wrap::after {
    content: "";
    display: block;
    clear: both; }
  .confirm-box .btn-yes, .confirm-box .btn-cancel {
    width: 50%;
    float: left;
    text-align: center;
    line-height: 30px;
    cursor: pointer;
    font-size: 14px; }
  .confirm-box .btn-yes {
    background: #38ce38; }
    .confirm-box .btn-yes:hover {
      background: #2cb52c; }
  .confirm-box .btn-cancel {
    background: #5a79e8;
    border-radius: 0 0 18px 0; }
    .confirm-box .btn-cancel:hover {
      background: #4a6adc; }

