.category {

  .category-wrap {
    width: 1200px;
    margin: 0 auto;
  }

  .category-head {
    margin: 25px 0;
  }
  .cho-wrap {
    width: 820px;
    margin: 0 auto;
    &::after {
      content: "";
      display: block;
      clear: both;
    }
  }

  .cho-onece {
    width: 240px;
    height: 400px;
    padding: 10px 0 10px 10px;
    overflow-y: auto;
    float: left;
    margin: 0 5px;
    border: 1px solid rgb(61, 51, 204);
    &::-webkit-scrollbar {
      -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
      width: 10px;     /*高宽分别对应横竖滚动条的尺寸*/
      height: 1px;
      border-radius: 10px;
      background: #eee;
    }
  }

  .cho-item {
    padding: 2px 0 2px 5px;
    margin-right: 10px;
    cursor: pointer;
    border: 1px solid transparent;

    &:hover {
      background-color: rgba(84, 84, 170, 0.1);
    }
  }

  .cho-chosen {
    background-color: rgba(53, 53, 255, 0.1) !important;
    color: #444;
    font-weight: bold;
    border: 1px solid rgba(32, 32, 180, 0.2);
  }

  .current-cate-wrap {
    width: 820px;
    margin: 20px auto;
    // background: #ccc;
  }
  .curr-cate-box {
    width: 770px;
    padding: 10px;
    background: #e0ecff;

  }
  .curr-item {
    font-weight: bold;
    color: #ff5400;
  }
  .curr-gt {
    margin: 0 20px;
    color: #7d6767;
  }

  .c-submit {
    height: 40px;
    width: 300px;
    border-radius: 4px;
    font-size: 16px;
    font-weight: bold;
    border: 1px solid #fff;
    padding: 5px;
    margin: 0 auto;
    background-color: #f40;
    text-align: center;
    line-height: 40px;
    cursor: pointer;
    user-select: none;
    background-color: rgb(64, 150, 231);
    color: rgb(255, 255, 255);
    margin-top: 30px;
  }

  .c-submit-disable {
    background-color: rgb(244, 244, 244);
    color: rgb(170, 170, 170);
  }

  .readme {
    border: 1px solid #ccc;
    padding: 5px 10px 5px 20px;
    overflow-y: auto;
    width: 820px;
    height: 200px;
    margin: 30px auto 50px auto;
  }
}
