// 上一页，下一页按钮
.u-o-page {
  margin-top: 10px;
  user-select: none;
  &::after {
    content: "";
    display: block;
    clear: both;
  }
  .u-o-prev,.u-o-next {
    float: right;
    // width: 60px;
    height: 24px;
    line-height: 22px;
    background-color: #fff;
    border: 1px solid #dcdcdc;
    color: #3c3c3c;
    padding: 0 12px;
    border-radius: 3px;
    font-size: 12px;
    cursor: pointer;
  }
  .u-o-prev{
    margin-right: 10px;
  }

  .u-o-prev-void,.u-o-next-void {
    color: #9c9c9c;
    cursor: not-allowed;
  }
}
