@charset "UTF-8";
.issue {
  background: #f7f8fa; }
  .issue .require {
    position: relative;
    padding: 0 10px; }
    .issue .require::before {
      content: "*";
      color: #f40;
      margin-top: 10px;
      position: absolute;
      bottom: -4px;
      left: 0px;
      font-weight: bold; }
  .issue .f-important {
    color: #ff6600; }
  .issue .curr-item {
    font-weight: bold;
    color: #ff5400; }
  .issue .curr-gt {
    margin: 0 20px;
    color: #7d6767; }
  .issue .issue-head {
    background: #fff;
    padding: 10px 0 20px 0; }
    .issue .issue-head .f1 {
      color: #f40;
      font-size: 30px; }
    .issue .issue-head .f2 {
      color: #333;
      font-size: 20px; }
  .issue .ih-box {
    width: 1000px;
    margin: 0 auto; }
  .issue .issue-body {
    width: 1000px;
    margin: 0 auto; }
  .issue .issue-item {
    margin-top: 30px;
    background-color: white;
    padding: 20px; }
  .issue .iss-tit {
    padding-bottom: 10px;
    border-bottom: 1px solid #ccc; }
  .issue .iss-mini-wrap {
    padding: 0 40px; }
  .issue .iss-detail {
    padding: 20px 0;
    line-height: 25px; }
  .issue .good-tit input[type=text] {
    width: 760px;
    height: 25px;
    padding: 0 7px; }
  .issue .iss-spec {
    padding: 0 0 10px 0; }
  .issue .prop-wrap {
    width: 700px;
    margin: 0 auto; }
    .issue .prop-wrap input {
      height: 20px;
      width: 250px;
      padding: 3px 7px;
      margin: 0 0 7px 0;
      color: #555;
      border-color: #ccc;
      border: 1px dashed #aaa; }
      .issue .prop-wrap input:hover + .prop-cancel {
        display: block; }
  .issue .iss-onece {
    padding: 10px 0; }
  .issue .prop-name-wrap {
    position: relative; }
    .issue .prop-name-wrap:hover .prop-cancel {
      display: block; }
  .issue .prop-value-wrap {
    margin-left: 20px; }
  .issue .prop-value {
    position: relative; }
  .issue .prop-cancel {
    display: none;
    width: 16px;
    height: 16px;
    cursor: pointer;
    position: absolute;
    right: 5px;
    top: 6px; }
    .issue .prop-cancel:hover {
      display: block; }
  .issue .iss-table-wrap {
    margin: 30px 0 10px 0; }
  .issue .issue-table {
    border-collapse: collapse; }
    .issue .issue-table td {
      padding: 5px 10px; }
    .issue .issue-table input[type=text] {
      border: 0; }
  .issue .issue-thead {
    background: #e4f2f5;
    font-weight: bold; }
  .issue .iss-pics-wrap {
    padding-bottom: 80px; }
  .issue .iss-pic-item-wrap {
    margin-top: 40px; }
  .issue .iss-pic-tit {
    padding: 10px 20px;
    color: #444;
    font-size: 14px;
    font-weight: bold; }
    .issue .iss-pic-tit::before {
      content: "*";
      display: inline;
      color: #f40;
      padding: 0 3px; }
  .issue .pic-item {
    position: relative;
    width: 140px;
    height: 140px;
    border: 2px solid #ccc;
    overflow: hidden;
    line-height: 150px;
    text-align: center;
    margin: 0 19px;
    padding: 5px; }
    .issue .pic-item img {
      max-width: 140px;
      position: relative;
      z-index: 10; }
    .issue .pic-item input[type=file] {
      position: absolute;
      z-index: 99;
      bottom: 0;
      left: 0;
      width: 140px;
      height: 140px;
      opacity: 0; }
    .issue .pic-item::before {
      content: "点击添加图片";
      display: block;
      color: #666;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 0; }
    .issue .pic-item .pic-cancel {
      display: block;
      position: absolute;
      z-index: 9999;
      width: 150px;
      height: 30px;
      bottom: 0;
      left: 0;
      color: #fff;
      text-align: center;
      line-height: 30px;
      background: rgba(0, 0, 0, 0.5);
      cursor: pointer;
      display: none; }
    .issue .pic-item:hover .pic-cancel {
      display: block; }
  .issue .iss-submit {
    padding-bottom: 50px;
    background: #fff; }
    .issue .iss-submit button {
      display: block;
      margin: 0 auto;
      width: 200px;
      height: 50px;
      border-radius: 8px;
      border: 0;
      background: #f40;
      color: #fff;
      font-weight: bold;
      cursor: pointer; }
