/**
 * 登录页
 */
.login-body {

}

// 登录页头部
.login-head {
  height: 100px;
  width: 1200px;
  margin: 0 auto;
  padding: 10px 0;
}
.main-logo {
  height: 100%;
  &>img {
    height: 100px;
  }
}
.login-msg {
  background: #f5e3ff;
  border: 1px solid #c19ad0;
  color: #666;
  font-size: 12px;
  padding: 5px 40px;
  margin-top: 35px;
}

.login-wrap {
	background-color: #ba34eb;
	background: url('http://118.126.108.36/image/seller/login_wall.jpg') no-repeat center;
}
.login-title {
	// background-color: #fff8f0;
	height: 38px;
}
.login-form {
	height: 475px;
	width: 990px;
	margin: 0 auto;
	position: relative;
	.form {
		position: absolute;
		right: 0;
    top: 50%;
    margin-top: -168px;
    height: 320px;
		width: 350px;
		background-color: #f6e8ff;

	}
}
.form-t{
	padding: 20px 0;
  border-bottom: 1px solid #f4f4f4;
  font-size: 18px;
	&::after {
		content: "";
		display: block;
		clear: both;
	}
	div {
		width: 50%;
		text-align: center;
		cursor: pointer;
		&:hover {
			color: #c779f1;
    		font-weight: bold;
		}
	}
	.f1 {
		// 选中字体
		color: #ba34eb;
    	font-weight: bold;
	}
	.f2 {
		// 未选字体
		color: #666;
	}
	.t-l {
		float: left;
		box-sizing: border-box;
		border-right: 1px solid #f4f4f4;
	}
	.t-r {
		float: right;
	}
}
.to-registory {
  // margin-top: 90px;
  text-align: right;
  color: #333;
  font-size: 12px;
  padding: 0 20px;
  position: absolute;
  left: 10px;
  bottom: 30px;
}

/**
 * 手机注册页
 */
.l-input-wrap {
	width: 304px;
	margin: 20px auto;
	label {
		width: 39px;
		height: 40px;
		float: left;
		display: block;
		border-right: 1px solid #ddd;
	}
	input {
		display: block;
		float: left;
		height: 40px;
		border: 0;
		padding: 0 8px;
		box-sizing: border-box;
	}
	&>div {
		width: 306px;
		height: 40px;
		margin-bottom: 20px;
		border: 1px solid #ddd;
	}

  .acc-log-err {
    border: 0;
    color: red;
    text-align: right;
    height: 20px;
    margin: 0;
    margin-top: -10px;
    font-size: 12px;
  }
  .acc-log-err-submit {
    margin-top: 70px;
  }
}
.log-input1 {
	input {
		width: 265px;
    outline: none;
	}
	label {

	}
}


.account-log-submit {
  display: block;
  margin: 0 0 0 20px;
  width:250px;
  height:38px;
  background-color:#ba34eb;
  font-size:16px;
  color:#fff;
  text-align:center;
  line-height:40px;
  border-radius:5px;
  border: 0;
  cursor: pointer;
}


// 短信登录
.log-input2 {
  position: relative;
  input {
    width: 265px;
    outline: none;
  }
}
.log-sendsms {
  width: 100px;
  height: 40px;
  position: absolute;
  right: 0;
  cursor: pointer;
  background: #d0b5f9;
  border: none;
  color: #666;
  text-align: center;
  line-height: 40px;
  outline: none;
}
