body {
  margin: 0;
  font-family: 微软雅黑, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
  color: #666;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

input,button {
  outline: none;
}

body, button, dd, dl, dt, form, h1, h2, h3, h4, h5, h6, hr, input, legend, li, ol, p, pre, td, textarea, th, ul {
    margin: 0;
    padding: 0;
}
a {
  text-decoration: none;
  color: #666;
  &:hover {
    color: #e22;
  }
}
h1,h2,h3,h4 {
  text-align: center;
}
p {
  padding: 0;
  margin: 0;
  text-align: center;
  font-size: 12px;
}
ul,li {
  list-style:none;
  margin: 0;
  padding: 0;
}

.f_wrap {

  &::after {
    content: "";
    display: block;
    clear: both;
  }
}
.f_L {
  float: left;
}
.f_r {
  float: right;
}

// start react-free-scrollbar 滚动条
.FreeScrollbar-vertical-track {
  background-color: transparent;
  width: 10px;
  transition: opacity 0.3s;
}

.FreeScrollbar-horizontal-track {
  background-color: transparent;
  height: 10px;
  transition: opacity 0.3s;
}

.FreeScrollbar-vertical-handler {
  width: 8px;
  right: 1px;
  border-radius: 4px;
  background-color: rgba(111, 110, 122, 0.2) !important;
  transition: opacity 0.3s;
  &:hover {
      background-color: rgba(121, 106, 106, 0.3) !important;
  }
}

.FreeScrollbar-horizontal-handler {
  height: 8px;
  bottom: 1px;
  border-radius: 4px;
  background-color: rgba(0, 0, 0, 0.5);
  transition: opacity 0.3s;
  &:hover {
      background-color: rgba(0, 0, 0, 0.8);
  }
}
